<template>
  <div id="nav">
    <h2>How many values you want to generate (between 0 and 9):</h2>
    <input v-model="number" name="number"/>
    <button type="submit" @click="submitForm">Generate</button>
    <ul v-for="(val, index) in items" :key="index">
      {{val}}
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'App',
  data() {
    return {
      number: '',
      items: []
    }
  },
  methods: {
    submitForm() {
      var url = "https://2h0imyxybf.execute-api.us-east-2.amazonaws.com/default/RandomGenerator?n=" + this.number
      axios
        .get(url)
        .then(response => {
          this.items = response.data
          console.log(this.items)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>


